.cart-summary {
  width: 300px;
  border: 2px solid #e5e5e5;
  padding: 20px;
  font-family: Arial, sans-serif;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.5);
  border-radius: 8px;
  position: fixed;
  background-color: white;
  margin-top: 100px;
}

.cart-summary h2 {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 30px;
  margin-top: 0px;
}
  
.summary-details {
  margin-bottom: 15px;
}
  
.summary-row {
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  margin: 8px 0;
}
  
.summary-row.total {
  font-weight: bold;
  font-size: 18px;
  border-top: 1px solid #e5e5e5;
  padding-top: 10px;
}
  
.checkout-button-purchase{ 
  background-color: rgb(242, 141, 77);
  color: white; 
  padding: 10px 20px; 
  border-radius: 5px; 
  width: 100%;
  margin-top: 10px;
  font-size: 16px;
  cursor: pointer;
  border: none;
}
.checkout-button-purchase:disabled {
  background-color: #e0e0e0;
  color: #a0a0a0;
  cursor: not-allowed;
}

.checkout-button-checkout{ 
  background-color: rgb(242, 141, 77);
  color: white; 
  padding: 10px 20px; 
  border-radius: 5px; 
  width: 100%;
  margin-top: 10px;
  font-size: 16px;
  cursor: pointer;
  border: none;
}
.checkout-button-checkout:disabled {
  background-color: #e0e0e0;
  color: #a0a0a0;
  cursor: not-allowed;
}
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.popup-content {
  background: #fff;
  padding: 20px;
  border-radius: 8px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.popup-content h3 {
  margin: 0 0 10px;
}

.popup-content p {
  margin: 0 0 20px;
}

.close-popup-button {
  background: rgb(242, 141, 77);
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}